<template>
  <header id="site-header">
    <h1>
      <router-link class="flex items-center font-semibold" :to="toIndex">
        <svg
          class="w-5 h-5 mt-0.5 mr-2"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M9.647.343a1.362 1.362 0 011.284 0l8.363 4.47a.778.778 0 010 1.373l-7.978 4.264a1.946 1.946 0 00-1.029 1.712l-.009 4.545a.778.778 0 01-1.144.686L.413 12.757A.778.778 0 010 12.07V6.666c0-.718.395-1.378 1.029-1.717L9.647.343z"
            fill="url(#paint0_radial)"
          />
          <defs>
            <radialGradient
              id="paint0_radial"
              cx="0"
              cy="0"
              r="1"
              gradientUnits="userSpaceOnUse"
              gradientTransform="matrix(15.5632 7.52763 -3.99402 8.25753 .29 5.99)"
            >
              <stop stop-color="#575858" />
              <stop offset="1" stop-color="#383939" />
            </radialGradient>
          </defs>
        </svg>
        透明盒子计划{{ pageTitle }}
      </router-link>
    </h1>
    <div id="nav">
      <router-link v-if="$route.name === 'Notes'" class="ml-2" to="/essays"
        >通讯文章</router-link
      >
      <router-link v-else class="ml-2" to="/">工作笔记</router-link>
    </div>
  </header>
  <router-view />
  <footer id="site-footer">
    <span>&copy; {{ currentYear }} 透明盒子计划</span>
  </footer>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";

const darkTheme = window.matchMedia("(prefers-color-scheme: dark)");

@Options({
  beforeUpdate() {
    if (darkTheme.matches) this.removeImgEventListener();
  },
  updated() {
    if (darkTheme.matches) this.addImgEventListener();
  },
  mounted() {
    if (darkTheme.matches) this.addImgEventListener();
  },
  computed: {
    currentYear() {
      return new Date().getFullYear();
    },
    pageTitle() {
      if (this.$route.name === "Notes") {
        return "·笔记";
      } else if (this.$route.name === "Essays") {
        return "·文章";
      }
    },
    toIndex() {
      if (this.$route.name === "Essays") {
        return "/essays";
      }
      return "/";
    }
  },
  methods: {
    overImg(event) {
      const { target } = event;
      target.classList.add("clear");
    },
    leaveImg(event) {
      const { target } = event;
      target.classList.remove("clear");
    },
    removeImgEventListener() {
      [...document.getElementsByTagName("img")].forEach(img => {
        img.removeEventListener("mouseenter", this.overImg);
        img.removeEventListener("mouseleave", this.leaveImg);
      });
    },
    addImgEventListener() {
      setTimeout(() => {
        [...document.getElementsByTagName("img")].forEach(img => {
          img.addEventListener("mouseenter", this.overImg);
          img.addEventListener("mouseleave", this.leaveImg);
        });
      }, 200);
    }
  }
})
export default class App extends Vue {}
</script>
