
import { Options, Vue } from "vue-class-component";

const darkTheme = window.matchMedia("(prefers-color-scheme: dark)");

@Options({
  beforeUpdate() {
    if (darkTheme.matches) this.removeImgEventListener();
  },
  updated() {
    if (darkTheme.matches) this.addImgEventListener();
  },
  mounted() {
    if (darkTheme.matches) this.addImgEventListener();
  },
  computed: {
    currentYear() {
      return new Date().getFullYear();
    },
    pageTitle() {
      if (this.$route.name === "Notes") {
        return "·笔记";
      } else if (this.$route.name === "Essays") {
        return "·文章";
      }
    },
    toIndex() {
      if (this.$route.name === "Essays") {
        return "/essays";
      }
      return "/";
    }
  },
  methods: {
    overImg(event) {
      const { target } = event;
      target.classList.add("clear");
    },
    leaveImg(event) {
      const { target } = event;
      target.classList.remove("clear");
    },
    removeImgEventListener() {
      [...document.getElementsByTagName("img")].forEach(img => {
        img.removeEventListener("mouseenter", this.overImg);
        img.removeEventListener("mouseleave", this.leaveImg);
      });
    },
    addImgEventListener() {
      setTimeout(() => {
        [...document.getElementsByTagName("img")].forEach(img => {
          img.addEventListener("mouseenter", this.overImg);
          img.addEventListener("mouseleave", this.leaveImg);
        });
      }, 200);
    }
  }
})
export default class App extends Vue {}
